import axios from "axios";
axios.defaults.withCredentials = true

export const toyService = {
  query,
  deleteToy,
  save,
  getById,
  getEmptyToy,
};
function _getUrl(id=''){
  const BASE_URL=(process.env.NODE_ENV!=='development')? '/api/toy':'//localhost:3030/api/toy'
  return `${BASE_URL}/${id}`
}
function query(filterBy) {
  let filterQuery = `%3Fname%3D${filterBy.name}%26type%3D${filterBy.type}%26inStock%3D${filterBy.inStock}%26sort%3D${filterBy.sortBy}`;
  return axios
    .get(_getUrl()+filterQuery)
    .then((res) => res.data);
}
function getById(toyId) {
  return axios
    .get(_getUrl(toyId))
    .then((res) => res.data);
}

function deleteToy(toyId) {
  return axios
    .delete(_getUrl(toyId))
    .then((res) => res.data);
}
function _add(toy) {
  toy.createdAt = Date.now();
  toy.image=`https://robohash.org/${toy.name}`
  return axios.post(_getUrl(), toy).then((res) => res.data);
}

function _update(toy) {
  return axios
    .put(_getUrl(toy._id), toy)
    .then((res) => res.data);
}

function save(toy) {
  return toy._id ? _update(toy) : _add(toy);
}

function getEmptyToy(){
  return {
    name: "",
    price: 0,
    type: "",
    inStock: false,
  }
   
}
