<template>
  <header class="main-header">
    <user-msg/>
    <nav class="nav-bar flex space-between">
      <div class="logo">
        <img :src="require('@/assets/images/logo.jpg')" />
      </div>
      <h2 class="logged-title" v-if="loggedUser">Logged As - {{loggedUser.username}}</h2>
      <section class="links flex">
        <router-link
          tag="li"
          to="/"
        ><i class="fas fa-home fa-2x"></i></router-link>
        <router-link
          tag="li"
          to="/about"
        ><i class="fas fa-info-circle fa-2x"></i></router-link>
        <router-link
          tag="li"
          to="/dash"
          v-show="loggedUser"
        ><i class="fas fa-user-cog fa-2x"></i></router-link>
        <router-link
          tag="li"
          to="/login"
          v-if="!loggedUser"
        ><i class="fas fa-sign-in-alt fa-2x"></i></router-link>
        <router-link
          tag="li"
          to="/signup"
          v-show="!loggedUser"
        >Sign Up</router-link>
        <button class="logout-btn"
          @click="logout"
          v-show="loggedUser"
        >Log Out</button>
      </section>
    </nav>
  </header>
</template>

<script>
import userPanel from "../components/user-panel.cmp.vue";
import {eventBus,SHOW_MSG} from '../services/event-bus-service.js'
import userMsg from './user-msg.cmp.vue'

export default {
  name: "app-header",
  computed: {
    loggedUser(){
    return this.$store.getters.loggedUser;
    }
  },
  methods: {
    logout() {
      this.$store.dispatch({ type: "logout" });
      eventBus.$emit(SHOW_MSG,"Logged Out Sucssfully")
      setTimeout(()=>this.$router.push('/'),1000) 
    }
  },
  components: {
    userPanel,
    userMsg,
  }
};
</script>

