<template>
  <div class="toy-reviews">
      <form @submit.prevent="addReview" class="review-form col-layout">
        <input type="text" placeholder="Your Review" v-model="userReview.content" maxlength="120" >
        <button class="add-review-btn">Send</button>
      </form>
    <section class="reviews" v-if="reviews.length!==0">
      <ul v-if="reviews">
        <li v-for="review in reviews" :key="review._id">
           <avatar :username="review.username"></avatar>
           <h3>{{review.content}}</h3>
        </li>
      </ul>
    </section>
    <h1 v-else>Be The First to Review This Awsome Robot!</h1>
  </div>
</template>

<script>
import avatar from "vue-avatar";
import { eventBus ,SHOW_MSG} from '../services/event-bus-service';
export default {
  name: "toy-reviews",
  props:["toyId"],
  data() {
    return {
      userReview: {
        content: ""
      },
      reviews: [],
    };
  },
  async created(){
      const reviews= await this.$store.dispatch({type:"currToyReviews",toyId:this.toyId})
      this.reviews=reviews  
      },
  methods: {
    async addReview() {
      const user=this.$store.getters.loggedUser
      if(!user){
        eventBus.$emit(SHOW_MSG,"Login To Review")
        return
      }
      this.userReview.toyId=this.$route.params.id
      await this.$store.dispatch({type:"addReview",review:{...this.userReview}})
      this.reviews=this.$store.getters.currReviews
    },
  },
  components: {
    avatar
  }
};
</script>

