<template>
  <div
    class="toy-filter"
    v-if="filterBy"
  >
    <section class=" options flex">
      <el-input
        type="text"
        v-model="filterBy.name"
        placeholder="Enter Toy Name.."
        @input="filter"
        class="name-search"
      ></el-input>
      <el-select
        v-model="filterBy.type"
        clearable
        @change="filter"
      >
        <el-option
          v-for="item in typeOptions"
          :key="item.value"
          :label="item.label"
          :value="item.value"
        >
        </el-option>
      </el-select>

      <el-select
        v-model="filterBy.inStock"
        clearable
        @change="filter"
      >
        <el-option
          v-for="item in stockOptions"
          :key="item.value"
          :label="item.label"
          :value="item.value"
        >
        </el-option>
      </el-select>
      <el-select
        clearable
        @change="filter"
        v-model="filterBy.sortBy"
      >
        <el-option
          v-for="item in sortOptions"
          :key="item.value"
          :label="item.label"
          :value="item.value"
        >
        </el-option>
      </el-select>
    </section>
  </div>
</template>

<script>
export default {
  name: "toy-filter",
  data() {
    return {
      filterBy: {
        name: '',
        inStock: "all",
        type: "all",
        sortBy: "name"
      },
      typeOptions: [
        { value: "all", label: "All" },
        { value: "Superheros", label: "Superheros" },
        { value: "Humanoids", label: "Humanoids" },
        { value: "Drones", label: "Drones" },
        { value: "Entertaiment", label: "Entertaiment" },
        { value: "Educational", label: "Educational" },
        { value: "Funny", label: "Funny" }
      ],
      stockOptions: [
        { value: "all", label: "All" },
        { value: true, label: "In Stock" },
        { value: false, label: "Out Of Stock" }
      ],
      sortOptions: [
        { value: "name", label: "Name" },
        { value: "price", label: "Price" }
      ]
    };
  },

  computed: {
    filter() {
      return this.debounce(this.setFilter, 500);
    }
  },
  methods: {
    debounce(filterFunction, delay) {
      let timer;
      return function() {
        clearTimeout(timer);
        timer = setTimeout(() => {
          filterFunction();
        }, delay);
      };
    },
    setFilter() {
      this.$emit("filter", this.filterBy);
    }
  },
  wathc: {
    $route() {
      this.filterBy = {
        name: "",
        inStock: "all",
        type: "all",
        sortBy: "name"
      };
    }
  }
};
</script>
