import { reviewService } from "../services/review-service.js";

export const reviewStore = {
  state: {
    reviews: null,
    currToyReviews:null
  },
  getters: {
    reviews(state) {
      return state.reviews;
    },
    currReviews(state){
      return state.currToyReviews
    }
  },
  mutations: {
    setReviews(state, { reviews }) {
      state.reviews = reviews;
    },
    setCurrReviews(state, { reviews }) {
      state.currToyReviews = reviews;
    },
    addReviewToToy(state,{review}){
      state.currToyReviews.unshift(review)
    },
    removeReview(state,{review}){
      const idx=state.reviews.findIndex(r=>r._id===review._id)
      state.reviews.splice(idx,1)
    }
  },
  actions: {
    async getReviews({ commit },{user}) {
      const reviews=await reviewService.query(user);
      commit({type:'setReviews',reviews})
      return reviews;
    },
    async addReview({ commit }, {review}) {
      const savedReview = await reviewService.add(review);
      commit({type:'addReviewToToy',review:savedReview})
    },
    async currToyReviews({ commit }, {toyId}) {
      const reviews = await reviewService.getById(toyId);
      commit({type:"setCurrReviews",reviews})
      return reviews;
    },
    async deleteReview({ commit }, {reviewId}) {
      await reviewService.remove(reviewId);
    },
   
   
  },
};
