<template>
  <div class="toy-app main-layout" >
    <toy-filter @filter="setFilter" />
    <toy-list :toys="toys" @delete="deleteToy" />
  </div>
</template>

<script>
import toyList from "../components/toy-list.cmp.vue";
import toyFilter from "../components/toy-filter.cmp.vue";
import {eventBus,SHOW_MSG} from '../services/event-bus-service.js'

export default {
  name: "toy-app",
  created() {
    this.$store
      .dispatch({ type: "loadToys"})
  },
  computed:{
    toys(){
      return this.$store.getters.toys
    }
  },
  methods: {
    deleteToy(toyId) {
      this.$store.dispatch({ type: "deleteToy", id: toyId })
      .then(()=>{eventBus.$emit(SHOW_MSG,'Toy Deleted!')})
      .catch(()=>eventBus.$emit(SHOW_MSG,'Only Admin Can Delete.'))
    },
    setFilter(filterBy) {
      this.$store.commit({type:'setFilterBy',filterBy:{...filterBy}})
      this.$store.dispatch({ type: 'loadToys' })
    },
  },
  components: {
    toyList,
    toyFilter,
  },
};
</script>


