import axios from "axios";
axios.defaults.withCredentials = true;

export const reviewService = {
  query,
  remove,
  add,
  getById,
};
function _getUrl(id = "") {
  const BASE_URL =
    process.env.NODE_ENV !== "development"
      ? "/api/review"
      : "//localhost:3030/api/review";
  return `${BASE_URL}/${id}`;
}
async function query(byUser) {
  let byUserId = !byUser ? "" : `%3FuserId%3D${byUser._id}`;
  const res = await axios.get(_getUrl() + byUserId);
  return res.data;
}
async function getById(toyId) {
  const review = await axios.get(_getUrl(toyId));
  return review.data;
}

async function remove(reviewId) {
  const removed = await axios.delete(_getUrl(reviewId));
  return removed.data;
}
async function add(review) {
  const savedReview = await axios.post(_getUrl(), review);
  console.log(savedReview)
  return savedReview.data;
}
