<template>
  <div class="toy-edit main-layout" v-if="toy">
    <header class="edit-header">
      <h1 class="cmp-title">{{ getTitle }}</h1>
      <i class="fas fa-arrow-alt-circle-left" @click="goHome"></i>
    </header>
    
    <form  class="details flex col-layout">
       <section class="item toy-item-name">
      <el-input type="text" v-model="toy.name" placeholder="Toy Name"></el-input>
      </section>
       <section class="item toy-item-type">
      <el-select v-model="toy.type" clearable placeholder="Type">
        <el-option v-for="item in typeOptions" :key="item.value" :label="item.label" :value="item.value"></el-option>
      </el-select>
      </section>
      <section class="item toy-item-price">
        <h4>Price</h4>
      <el-input-number v-model="toy.price" :min="1"></el-input-number>
      </section>
      <section class="item toy-item-stock">
      <el-switch 
      v-model="toy.inStock"   
      active-color="#b3d34c"
      inactive-color="#ff4949" 
      active-text="In Stock!" 
      inactive-text="Out Of Stock...">
      </el-switch>
      </section>
      <el-button round @click.prevent="saveToy" >Save</el-button>
    </form>
  </div>
</template>

<script>
import { toyService } from "../services/toy-service.js";

export default {
  name: "toy-edit",
  data() {
    return {
      toy: null,
       typeOptions: [
        { value: "Superheros", label: "Superheros" },
        { value: "Humanoids", label: "Humanoids" },
        { value: "Drones", label: "Drones" },
        { value: "Entertaiment", label: "Entertaiment" },
        { value: "Educational", label: "Educational" },
        { value: "Funny", label: "Funny" }
      ],
    };
  },
  created() {
    this.loadToy()
  },
  methods: {
    goHome() {
      this.$router.push("/");
    },
    loadToy() {
      let id = this.$route.params.id;
      if (id) {
        toyService.getById(id).then((toy) => (this.toy = toy));
      } else this.toy = toyService.getEmptyToy();
    },
    saveToy() {
      let toy = JSON.parse(JSON.stringify(this.toy));
      this.$store.dispatch({ type: "saveToy", toy });
      this.$router.push("/");
    },
  },
  computed: {
    getTitle() {
      if (!this.toy._id) return "Add New Toy:";
      return "Edit Toy Details:";
    },
  },
  watch: {
    '$route.params.id':()=>{
      this.loadToy()
    },
  },
};
</script>
