<template>
  <div class="user-dash">
    <line-chart></line-chart>
    <div
      class="user-reviews"
      v-if="reviews.length!==0"
    >
      <section class="filter-reviews">
        <button @click="reviewsToShow">All Reviews</button>
        <button @click="reviewsToShow('user')">My Reviews</button>
      </section>
      <div
        class="review flex space-between "
        v-for="review in reviews"
        :key="review._id"
      >
        <section class="flex col">
          <h2><span>Reviewd Toy:</span> {{review.toyId}}</h2>
          <h3><span>Content:</span> {{review.content}}</h3>
          <h3><span>Published:</span> {{calcTime(review.published)}}</h3>
        </section>
        <i class="fas fa-comment fa-3x" @click="goToToy(review.toyId)"></i>
      </div>
    </div>
    <h1 v-else>You Haven't Reviewd Any of Our Toys !</h1>
  </div>
</template>

<script>
import LineChart from "../components/chart-cmp.vue";

export default {
  name: "user-dash",
  data() {
    return {
      datacollection: null,
      reviews: [],
      reviewsFilter: "user"
    };
  },
  created() {
    this.loadReviews();
  },
  methods: {
    reviewsToShow(filter) {
      this.reviewsFilter = filter;
      this.loadReviews();
    },
    async loadReviews() {
      let user =
        this.reviewsFilter === "user" ? this.$store.getters.loggedUser : null;
      this.reviews = await this.$store.dispatch({ type: "getReviews", user });
    },
    calcTime(time) {
      var timeStamp = new Date(time).toLocaleDateString() + ",";
      timeStamp += new Date(time).toLocaleTimeString();
      return timeStamp;
    },
    goToToy(id){
      this.$router.push(`/toy/${id}`)
    }
  },

  components: {
    LineChart
  }
};
</script>

