
<script>
import { Pie } from "vue-chartjs";

export default {
  extends: Pie,
  data: () => ({
    chartdata: {
      datasets: [
        {
          label: "Adult",
          backgroundColor: "#f87979",
          data: [20]
        },
           {
          label: "Funny",
          backgroundColor: "green",
          data: [40]
        },
           {
          label: "Educational",
          backgroundColor: "yellow",
          data: [50]
        }
      ]
    },
    options: {
      responsive: true,
      maintainAspectRatio: false
    }
  }),

  mounted() {
    this.renderChart(this.chartdata, this.options);
  }
};
</script>

